:root {
  --color6 : rgba(50,50,50);
  --color5 : #10454F;
  --color4 : #506266;
  --color3 : #818274;
  --color2 : #A3AB78;
  --color1 : #BDE038;
  --color0 : #FCFFA7;
}

body, *, ::before, ::after {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
}

body {
  background-color: var(--color0);
}

#mainBody {
  margin: 20vh auto 0;
  width: 350px;
  
}

#calculator {
  height: 400px;
  padding: 10px;
  background-color: var(--color2);
  border-radius: 5px;
}

#screen {
  height: 80px;
  padding: 8px 8px;
  background-color: var(--color6);
  border-radius: 5px;
  text-align: right;
}

#preview {
  height: 20px;
  color: var(--color1);
  font-size: 120%;
  margin-bottom: 6px;
}

#display {
  color: white;
  font-size: 200%;
}

#buttons {
  text-align: center;
  height: 300px;
  padding: 10px 0 0;
  display : grid;
  grid-template-columns: repeat(4,1fr);
  grid-template-rows: repeat(5,1fr);
  gap: 10px;
}

#allClear {
  grid-column: 1/3;
  grid-row: 1/2;
}

#entryClear {
  grid-column: 3/-2;
  grid-row: 1/2;
}

#math {
  grid-column: -2/-1;
  grid-row: 1/-1;
  display: grid;
  grid-template-rows: repeat(5,1fr);
  grid-auto-rows: auto;
  gap: 10px;
}

#numbers {
  grid-column: 1/-2;
  grid-row: 2/-2;
  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid-auto-columns: auto;
  gap: 10px;
}

#zero {
  grid-column: 1/3;
  grid-row: -2/-1;
}

#decimal {
  grid-column: 3/-2;
  grid-row: -2/-1;
}


#buttons button {
  border: 0;
  font-size: 130%;
  border-radius: 5px;
}

#buttons button:not(.highlight) {
  color: white;
}

#math button:not(.highlight) {
  background-color: var(--color4);
}

#numbers button, #zero, #decimal {
  background-color: var(--color3);
}


.highlight {
  background-color: var(--color1);
  color: var(--color4);
  font-weight: 500;
}


#buttons button:not(.highlight):hover {
  color: var(--color1);
  box-shadow: inset 1px 1px 2px var(--color4);
}

.highlight:hover {
  color: var(--color0);
  box-shadow: inset 1px 1px 2px var(--color4);
}

footer {
  margin-top: 20px;
  color: var(--color2);
  text-align: center;
  font-family: fantasy;
}